import { Routes, Route } from 'react-router-dom'

import { Home, Catalogue } from '../pages' 
import Details from '../pages/detail/Details'

function ThreeMainRoutes() {
  return (
    <Routes>
        <Route 
            path='/:category/search/:keyword'
            element={<Catalogue />}    
        />

        <Route 
            path='/:category/:id'
            element={<Details />}    
        />

        <Route 
            path='/:category'
            element={<Catalogue />}    
        />

        <Route 
            path='/'
            exact
            element={<Home />}    
        />
        
    </Routes>
  )
}

export default ThreeMainRoutes
